  display: none; /* Chrome, Safari, Opera */
}

.plusStar {
  position: relative;
  background-color: #d9d9d9;
  display: inline-block;
  width: 75px;
  height: 75px;
  border-radius: 100%;
  margin-right: 10px;
}

.plusStarBox {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.plusStarText {
  position: absolute;
  color: #ffffff;
  font-size: 30px;
}
.category {
  font-family: 'SCD500';
  font-size: 20px;
  margin-bottom: 10px;
}

.challengeContainer {
  overflow: auto;
  white-space: nowrap;
}

.img {
  width: 100%;
  object-fit: cover;
}

.coverBox {
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.3);
  position: absolute;
  width: 144px;
  height: 256px;
  opacity: 1;
}
.blankBox {
  flex: 3;
}
.infoBox {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding-left: 5px;
  padding-right: 5px;
  padding-bottom: 5px;
}
.cardWrapper {
  position: relative;
  display: inline-block;
  margin-right: 10px;
  border-radius: 5%;
  width: 144px;
  height: 256px;
  border-width: 1px;
  border-style: solid;
  overflow: hidden;
}
.reactPlayer {
  pointer-events: none;
}

.infoTop {
  display: flex;
  flex-direction: column;
}
.infoBot {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.infoText {
  color: #ffffff;
  font-size: 10px;
  margin-bottom: 2px;
}

.progressDiv {
  background-color: #ffffff;
  border-radius: 0.5rem;
}
.progress {
  background-color: rgba(41, 47, 51, 0.68);
  height: 15px;
  border-bottom-left-radius: 0.5rem;
  border-top-left-radius: 0.5rem;
  transition: 1s ease;
  transition-delay: 0.5s;
}

/* 기본 모양 */
.ellipsis {
  max-width: 300px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* 540px 이상 */
@media (min-width: 540px) {
  .ellipsis {
    max-width: 500px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

.title {
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 18px;
}

.groupBox {
  margin-top: 30px;
}

.group {
  margin-bottom: 40px;
}

.content {
  font-weight: bold;
  font-size: 18px;
  text-align: center;
}

.btn {
  background-color: transparent; /* Blue background */
  border: none; /* Remove borders */
  color: black; /* White text */
  padding: 0; /* Some padding */
}

